.header {
  padding-top: 67px;
  text-align: center;
  position: relative;
  margin-bottom: 30px; }

.header__pic {
  display: block;
  margin: 0 auto 30px;
  position: relative;
  left: -10px; }

.header__title {
  @include f(b); }

.header__aside-btn {
  position: absolute;
  bottom: 0;
  right: 20px; }

.attendance {
  padding: 37px 0 0;
  background: #529EF7;
  color: white;
  text-align: center;
  font-size: 18px; }

.attendance__title {
  margin-bottom: 16px;
  @include f(b, 32, 34); }

.attendance__footer {
  background: #4290EC;
  margin-top: 80px;
  padding: 10px 0 50px; }

.attendance__slider {
  margin: 0 60px;
  position: relative;
  padding-bottom: 30px; }

.check-list {
  white-space: nowrap;
  &:not(:last-child) {
    padding-right: 15px; } }

.check-list__item {
  &:not(:first-child) {
    margin-top: 15px; }
  > * {
    vertical-align: middle; } }

.callback-form {
  text-align: center;
  font-size: 18px;
  .field {
    width: 100%;
    max-width: 300px;
    margin-left: auto;
    margin-right: auto; }
  .title {
    margin-bottom: 13px; } }

.affiliate-form {
  text-align: center;
  .field {
    width: 100%;
    max-width: 450px;
    margin-left: auto;
    margin-right: auto; } }

.form-text-row {
  margin: 30px 0;
  font-size: 14px;
  &:first-child {
    margin-top: 0; }
  &:last-child {
    margin-bottom: 0; }
  small {
    font-size: 12px; } }


.cta-input-group {
  position: relative;
  text-align: center;
  margin: 30px auto 90px;
  .field {
    width: 380px;
    margin: 0 5px 0 0;
    .field__input {
      height: 50px;
      font-size: 20px;
      background: $color-bg-main; }
    .field__left-icon,
    .field__right-icon {
      line-height: 48px; }
    &.is-filled.is-focus {
      .field__label {
        background: $color-bg-main;
        transform: translate3d(-5px, -31px, 0); } } }
  .cta-btn {
    margin: 0; }
  > * {
    display: inline-block;
    vertical-align: middle; } }

.cta-input-group__link {
  width: 0;
  white-space: nowrap;
  position: relative;
  left: 15px;
  font-size: 14px; }

.affiliate {
  max-width: 960px;
  padding: 60px 20px 60px;
  margin: auto; }

#btn-confirm {
  margin-bottom: 20px; }
