body {
  background: #FBFBFB;
  color: #546475;
  font-family: "myriad-set-pro_text", sans-serif;
  font-weight: normal;
  font-size: 15px;
  line-height: 1.4;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 940px;
}

body.modal-open {
  overflow: hidden;
}

body.modal-open .l-content-wrapper {
  filter: blur(4px);
}

body.page-with-sidebar {
  height: 100%;
  min-width: 1240px;
  *zoom: 1;
}

body.page-with-sidebar:after {
  content: " ";
  display: table;
  clear: both;
}

body.page-with-sidebar .l-content-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  min-width: 1240px;
}

body.sidebar-active .l-page-content {
  right: 300px;
}

body.sidebar-active .l-page-sidebar {
  transform: translateX(0);
}

.l-content-wrapper {
  transition: filter 0.3s ease;
}

.l-page-content,
.l-page-sidebar {
  position: absolute;
  top: 0;
  bottom: 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

.l-page-content {
  z-index: 0;
  left: 0;
  right: 0;
  transition: all 0.5s ease;
}

.l-page-sidebar {
  display: none;
  z-index: 1;
  right: 0;
  width: 300px;
  transform: translateX(100%);
  background: #F0F1F2;
  transition: all 0.5s ease;
}

