.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 998;
  background: rgba(255, 255, 255, 0.27);
}

.modal-backdrop.fade {
  transition: opacity 0.15s linear;
  opacity: 0;
}

.modal-backdrop.in {
  opacity: 1;
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  z-index: 999;
  white-space: nowrap;
  overflow: auto;
  text-align: center;
  font-size: 0;
  -webkit-overflow-scrolling: touch;
}

.modal:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.modal.fade .modal-dialog {
  transform: scale(1.1);
  transition: 0.3s ease;
  transition-property: opacity, transform;
  opacity: 0;
}

.modal.in .modal-dialog {
  transform: scale(1);
  opacity: 1;
}

.modal-dialog {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  max-width: 640px;
  font-size: 15px;
  text-align: left;
  white-space: normal;
}

.modal-dialog_w640 {
  max-width: 640px;
}

.modal-dialog_w800 {
